$("._toggle-catalog").click(function () {
    $(".header").toggleClass("_open")
    $(this).toggleClass("_active")
    if(window.innerWidth<=992){
        $("body").toggleClass("fixed-body2")
    }
})


$(document).mouseup(function(e) {
    var div = $('.header');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
        if ($(div).hasClass("_open")) {
            $(div).removeClass("_open")
        }
    }
});

$(".nav2__show").click(function () {
    $(this).toggleClass("_active")
    $(this).siblings(".dropdown").slideToggle()
})

$(".burger").click(function () {
    $(".header").toggleClass("_open-burger")
    $("body").toggleClass("fixed-body")
})

$("._level1 .menu-nav__item").mouseover(function () {
    if(window.innerWidth<=992){
        return
    }

    let tab = $(this).attr("data-nav")
    $("._level1 .menu-nav__item").removeClass("_active")
    $(this).addClass("_active")

    $("._level2").hide()
    $("._level3").hide()
    $("._level4").hide()

    $(".menu").removeClass("_level4-open")
    $(".menu").removeClass("_level3-open")
    $(".menu").addClass("_level2-open")

    $(`._level2 .menu-nav__items`).removeClass("_active")
    $(`._level2 .menu-nav__items[data-nav="${tab}"]`).addClass("_active")
    $("._level2").show()
})

