$(".socials-slider .swiper-slide:nth-child(1) .social-card__img").addClass("_active")

$(".social-card__wrap").hover(showSocial, ()=>{});

function showSocial() {
    if (window.innerWidth > 992) {
        let el = $(this)
        let id = $(el).attr("data-id")
        console.log(id)
        console.log(el)
        setTimeout(function() {
            if (el.is(':hover')) {
                $(".social-card__img").removeClass("_active")
                $(`.social-card__img[data-id="${id}"]`).addClass("_active")
            }
        }, 200);
    }
}

$(window).on('load resize', function(){
    if ($(window).width() <= 992) {
        $('.social-card__img').each(function (i,el) {
            let id = $(el).attr("data-id")
            $(el).insertAfter(`.social-card__wrap[data-id="${id}"]`)
        })
    } else{
        $('.social-card__img').insertAfter('.socials-slider');
    }
});
