let maps = document.querySelectorAll("[data-cord1]")

ymaps.ready(init);




function init(map){
    maps.forEach(map=> {
        createMap(map)
    })

    function createMap(map){
        let cord = [+map.dataset.cord1, +map.dataset.cord2]

        let myMap = new ymaps.Map(map, {
            center: cord,
            zoom: 16,
            controls: []
        });
    }
}
